<template>
  <div>
    <h3>{{label}}</h3>
    <div class="progress-line">
      <span :title="percent + '%'" ref="percentBox">{{percent}}%</span>
      <p :style="{width: percent + '%'}"></p>
    </div>
  </div>
</template>

<script>
  import {TimelineLite,TweenLite} from 'gsap';

  export default {
    name: 'ProgressBar',
    props: {
      label: String,
      percent: Number,
    },
    methods: {
      handleScroll() {
        const { percentBox } = this.$refs
        const timeline = new TimelineLite();
        console.log(window.scrollY, window.innerHeight + 1200)
        if (window.scrollY >= window.innerHeight + 1700) {

          timeline.to(percentBox, 1.5, { x: 910 * (this.percent/100)});
        }
      },
    },
    beforeMount () {
      window.addEventListener('scroll', this.handleScroll)
    },
    mounted() {
      console.log(window);
    },
    beforeDestroy () {
      window.removeEventListener('scroll', this.handleScroll)
    }
  }
</script>


<style lang="scss" scoped>
  @import "../../assets/styles/css-variables";

  h3 {
    margin-bottom: 24px;
  }

  .progress-line {
    position: relative;
    height: 3px;
    background-color: $color-gray;

    span {
      display: block;
      position: absolute;
      top: -21px;
      background-color: $color-background;
      width: 42px;
      height: 42px;
      font-size: 1rem;
      text-align: center;
      line-height: 42px;
    }

    p {
      height: 3px;
      background-color: $color-black;
    }
  }

</style>
